<template>
  <el-form
    :model="OrderDeliveryForm"
    :rules="OrderDeliveryRules"
    label-width="auto"
    size="small"
    ref="OrderDeliveryForm"
  >
    <el-form-item label="快递名称" prop="courier_name">
      <el-select
        v-model="OrderDeliveryForm.courier_name"
        placeholder="请选择发货快递"
      >
        <el-option label="圆通快递" value="1" />
        <el-option label="润达快递" value="2" />
        <el-option label="顺丰快递" value="3" />
      </el-select>
    </el-form-item>
    <el-form-item label="发货单号" prop="invoice_no">
      <el-input
        v-model="OrderDeliveryForm.invoice_no"
        placeholder="请输入发货单号"
      />
    </el-form-item>
  </el-form>
</template>

<script>
export default {
  data() {
    return {
      OrderDeliveryForm: {
        courier_name: "",
        invoice_no: ""
      },
      OrderDeliveryRules: {
        invoice_no: [
          { required: true, message: "请选择发货快递", trigger: "change" }
        ],
        courier_name: [
          { required: true, message: "请输入发货单号", trigger: "blur" }
        ]
      }
    };
  },
  methods: {
    validateForm() {
      let flag = false;
      this.$refs["OrderDeliveryForm"].validate(valid => {
        if (valid) {
          flag = this.OrderDeliveryForm;
        } else {
          flag = valid;
        }
      });
      return flag;
    }
  }
};
</script>

